.section__inner {
    padding: 25px;
    background-color: #fffff8;
    border-radius: 8px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
}

.section__title {
    color: #4d1609;
}

.section__subtitle {
    color: #7c4538;
}

.images-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
    margin-top: 1000px;
}

.image {
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #ccc;
    border: 5px solid #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    position: relative;

    &::before{
        content: url('./img/Group\ 1.svg');
        width: 100px;
        height: 100px;
        transform: translate(-50%, -50%);
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;
    }

    &::after {
        content: '';
        width: 30px;
        height: 120%;
        background: linear-gradient(180deg, rgba(243, 243, 243, 0.5), #ededed, rgba(243, 243, 243, 0.5));
        transform: rotate(15deg);
        -webkit-animation: lazyImage 5s linear infinite;
                animation: lazyImage 5s linear infinite;
        position: absolute;
        top: -10%;
        z-index: 2;
    }
}

img {
    display: block;
    max-width: 100%;
    height: auto;
    position: relative;
    z-index: 3;
}

@-webkit-keyframes lazyImage {
    0%{
        left: -10%;
    }
    50% {
        left: 120%;
    }
    100%{
        left: 120%;
    }
}

@keyframes lazyImage {
    0%{
        left: -10%;
    }
    50% {
        left: 120%;
    }
    100%{
        left: 120%;
    }
}
